import React, {
  Dispatch,
  FC,
  SetStateAction,
  useEffect,
  useState,
} from "react";

import { useFetch, useLocalStorage } from "hooks";
import { Button, notifyBarManager } from "components";
import { useGetLocale } from "providers";
import { ImgStatus } from "../..";
import css from "./style.module.scss";
import { track } from "utils";
interface Props {
  imageErrorStatus: ImgStatus;
  avatarFile: File | undefined;
  setImgSendStatus: Dispatch<SetStateAction<"none" | "loading" | "success">>;
  imgSendStatus: "loading" | "none" | "success";
}

interface BlobLoad {
  image_url: string;
  message: string;
  success: boolean;
}

interface ChangeAvatarResponse {
  message: string;
  success: boolean;
}

const SendImg: FC<Props> = ({
  imageErrorStatus,
  avatarFile,
  setImgSendStatus,
  imgSendStatus,
}: Props) => {
  const intl = useGetLocale();

  const [imgUrl, setImgUrl] = useState("");

  const [token] = useLocalStorage("token");
  const [setAvatar] = useFetch<ChangeAvatarResponse>(
    "account/update-profile-picture",
  );

  const showError = (error: string) => {
    try {
      notifyBarManager.error({
        message: error,
        timeOut: 4000,
      });

      console.error("Download Error: ", error);
    } catch (error) {
      track()?.errors(error);

      if (typeof error === "string") {
        console.error("Download Error: ", error);
      } else {
        console.error("Download Error ");
      }
    }
  };

  const sendImage = async () => {
    if (avatarFile) {
      const data = new FormData();
      data.append("image", avatarFile);

      setImgSendStatus("loading");

      await fetch("https://cdn.pasino.com/upload.php", {
        body: data,
        method: "POST",
      }).then(
        (res) => {
          if (res.ok) {
            res.json().then((data: BlobLoad) => {
              if (data.image_url) {
                setImgUrl(data.image_url);
                setImgSendStatus("success");
              }
            });
          }
        },
        (error) => {
          showError(error);
        },
      );
    }
  };

  const changeAvatar = async (url: string) => {
    const response = await setAvatar({
      variables: {
        image_url: url,
        token,
      },
    });

    if (response.data?.success) {
      notifyBarManager.success({
        message: response.data.message,
        timeOut: 4000,
      });
    }
  };

  useEffect(() => {
    if (imgUrl) {
      changeAvatar(imgUrl);

      setImgUrl("");
    }
  }, [imgUrl]);

  return (
    <div className={css.root}>
      <Button
        disabled={imageErrorStatus !== "good" || imgSendStatus !== "none"}
        className={css.sendButton}
        onClick={sendImage}>
        {intl.formatMessage({ id: "settings.profile.avatar.buttons.send" })}
      </Button>
    </div>
  );
};

export default SendImg;
