import React, { useMemo } from "react";
import { useGetLocale } from "providers";

import { ImgStatus } from "../..";
import css from "./style.module.scss";

interface Props {
  image: File | null | undefined;
  imageError: ImgStatus;
}

const ErrorMessage = ({ image, imageError }: Props) => {
  const intl = useGetLocale();

  const message = useMemo(
    () => ({
      error: (
        <p className={css.text} style={{ color: "red" }}>
          {intl.formatMessage({ id: "settings.profile.avatar.messages.error" })}
        </p>
      ),
      loading: (
        <p className={css.text} style={{ color: "white" }}>
          {intl.formatMessage({
            id: "settings.profile.avatar.messages.loading",
          })}
        </p>
      ),
      good: (
        <p className={css.text} style={{ color: "green" }}>
          {intl.formatMessage({ id: "settings.profile.avatar.messages.good" })}
        </p>
      ),
      wrong: (
        <p className={css.text} style={{ color: "red" }}>
          Something went wrong
        </p>
      ),
    }),
    [],
  );

  if (imageError !== "wrong" && !image) {
    return null;
  }

  return <>{message[imageError]}</>;
};

export default ErrorMessage;
