import React, { useEffect, useState } from "react";
import { useGetLocale } from "providers";

import { useFetch } from "hooks";
import { notifyBarManager, Typography } from "components";

import { Input, CopyInput } from "pages/ModalPages/componentsProgressive";
import { SubmitButton } from "pages/Auth/components";
import { TFA } from "..";

import { ReactComponent as WarningIcon } from "assets/icons/warn.svg";
import css from "./style.module.scss";

interface Enable2FA {
  message: string;
}

interface DisabledProps {
  TFAData: TFA;
  loading?: boolean;
  refetch: () => void;
}

const Disabled = ({
  TFAData,
  refetch,
  loading: parentLoading,
}: DisabledProps) => {
  const intl = useGetLocale();

  const [codeValue, setCodeValue] = useState("");
  const [fetch, { loading, data, error }] =
    useFetch<Enable2FA>("account/enable-tfa");

  useEffect(() => {
    if (loading) {
      return;
    }

    if (data) {
      notifyBarManager.success({
        message: data.message,
        timeOut: 4000,
      });
      setCodeValue("");
      refetch();
      return;
    }

    if (error) {
      notifyBarManager.error({
        message: error,
        timeOut: 4000,
      });
    }
  }, [data, error, loading, refetch]);

  const handleSubmit: React.FormEventHandler<HTMLFormElement> = (event) => {
    event.preventDefault();

    fetch({ variables: { code: codeValue } });
  };

  return (
    <>
      <div className={css.warningBlock}>
        <div className={css.warningIcon}>
          <WarningIcon />
        </div>
        <Typography className={css.warning}>
          {intl.formatMessage({ id: "settings.tfa.disabled.description" })}
        </Typography>
      </div>

      <form className={css.wrapper} onSubmit={handleSubmit}>
        <div className={css.row}>
          <div className={css.qrCodeWrapper}>
            <img className={css.qr} alt="QR" src={`${TFAData.qr_code_url}`} />
          </div>
          <div className={css.rowItem}>
            <Typography className={css.text}>
              Scan the QR Code and enter the code provided by Authenticator
              below:
            </Typography>
            <div className={css.codeInputWrapper}>
              <Input
                type="text"
                value={codeValue}
                label={intl.formatMessage({
                  id: "settings.tfa.disabled.codeText",
                })}
                onChange={({ target }) => setCodeValue(target.value)}
                required
              />
            </div>
          </div>
        </div>

        <CopyInput
          readOnly
          label={intl.formatMessage({ id: "settings.tfa.disabled.tokenText" })}
          value={TFAData.tfa_key}
        />

        <SubmitButton
          type="submit"
          loading={loading || parentLoading}
          className={css.submitButton}>
          {intl.formatMessage({ id: "settings.tfa.disabled.button" })}
        </SubmitButton>
      </form>
    </>
  );
};

export default Disabled;
