import React, { useEffect, useRef, useState } from "react";
import ReCAPTCHA from "react-google-recaptcha";

import { useFetch } from "hooks";
import { useGetLocale } from "providers";
import { notifyBarManager, Typography } from "components";

import { Input, InputProps } from "pages/ModalPages/componentsProgressive";
import { SubmitButton } from "pages/Auth/components";
import HideProfileSwitcher from "../HideProfileSwitcher";

import css from "./style.module.scss";

interface Mail {
  message: string;
}

const MailChange = () => {
  const intl = useGetLocale();

  const [fetch, { loading, data, error }] = useFetch<Mail>(
    "account/change-email",
  );

  const [email, setEmail] = useState("");
  const [emailError, setEmailError] = useState<string>();
  const captcha = useRef({
    token: "",
  });

  const handleChangeEmail: InputProps["onChange"] = (event) => {
    setEmail(event.target.value);
    setEmailError("");
  };

  useEffect(() => {
    if (loading) {
      return;
    }

    setEmailError(error);
  }, [error, loading]);

  const handleSubmit: React.FormEventHandler<HTMLFormElement> = async (
    event,
  ) => {
    event.preventDefault();

    fetch({
      variables: {
        email_address: email,
        re_response: captcha.current.token,
      },
    });
  };

  useEffect(() => {
    if (loading || error || !data?.message) {
      return;
    }

    setEmail("");
    notifyBarManager.success({
      message: data.message,
      timeOut: 4000,
    });
  }, [data, error, loading]);

  return (
    <div className={css.wrapper}>
      <form className={css.innerWrapper} onSubmit={handleSubmit}>
        <Typography variant="h2" className={css.title}>
          {intl.formatMessage({ id: "settings.email.title" })}
        </Typography>
        <Input
          id="settings-change-email-input"
          label={intl.formatMessage({ id: "settings.email.inputText" })}
          value={email}
          disabled={loading}
          onChange={handleChangeEmail}
          error={!!emailError}
          helperText={emailError}
          className={css.input}
        />
        <ReCAPTCHA
          sitekey={process.env.REACT_APP_RECAPTCHA_SITE_KEY as string}
          onChange={(token) => (captcha.current.token = token || "")}
        />
        <SubmitButton
          type="submit"
          loading={loading}
          className={css.submitButton}>
          {intl.formatMessage({ id: "settings.email.button" })}
        </SubmitButton>
        <HideProfileSwitcher />
      </form>
    </div>
  );
};

export default MailChange;
