import React, { useEffect, useState } from "react";
import { useGetLocale } from "providers";

import { useFetch } from "hooks";
import { notifyBarManager, Typography } from "components";

import { Input } from "pages/ModalPages/componentsProgressive";
import { SubmitButton } from "pages/Auth/components";

import css from "./style.module.scss";

interface Enabled2FA {
  message: string;
}

interface EnabledProps {
  loading?: boolean;
  refetch: () => void;
}

const Enabled = ({ refetch, loading: parentLoading }: EnabledProps) => {
  const intl = useGetLocale();

  const [codeValue, setCodeValue] = useState("");
  const [fetch, { data, error, loading }] = useFetch<Enabled2FA>(
    "account/disable-tfa",
  );

  useEffect(() => {
    if (loading) {
      return;
    }

    if (data) {
      notifyBarManager.success({
        message: data.message,
        timeOut: 4000,
      });
      setCodeValue("");
      refetch();
      return;
    }

    if (error) {
      notifyBarManager.error({
        message: error,
        timeOut: 4000,
      });
    }
  }, [data, error, loading]);

  const submit = (event: any) => {
    event.preventDefault();
    fetch({
      variables: {
        code: codeValue,
      },
    });
  };

  return (
    <form onSubmit={submit} className={css.wrapper}>
      <Typography className={css.text}>
        {intl.formatMessage({ id: "settings.tfa.enabled.description" })}
      </Typography>
      <Input
        className={css.codeInput}
        type="text"
        label={intl.formatMessage({ id: "settings.tfa.enabled.tokenText" })}
        value={codeValue}
        onChange={({ target }) => setCodeValue(target.value)}
        required
      />

      <SubmitButton
        type="submit"
        loading={loading || parentLoading}
        className={css.submitButton}>
        {intl.formatMessage({ id: "settings.tfa.enabled.button" })}
      </SubmitButton>
    </form>
  );
};

export default Enabled;
