import React, {
  Dispatch,
  SetStateAction,
  useCallback,
  useEffect,
  useState,
  CSSProperties,
} from "react";
import clsx from "clsx";
import { useGetLocale } from "providers";
import { ReactComponent as RotateLeftIcon } from "assets/icons/rotate-left.svg";
import { ReactComponent as UpArrow } from "assets/icons/expand-less.svg";

import "components/Cropper/cropper.css";

import { Button, Cropper, notifyBarManager, ReactCropper } from "components";

import { blobToFile } from "utils/blobToFile";
import noAvatarUrl from "./noAvatar.png";
import css from "./style.module.scss";
import { track } from "utils";

interface Props {
  isEdit: boolean;
  setIsEdit: Dispatch<SetStateAction<boolean>>;
  src: string;
  setAvatarFile: Dispatch<SetStateAction<File | undefined>>;
  setIsEditorImgHidden: Dispatch<SetStateAction<boolean>>;
}

const rotatedIconStyle: CSSProperties = {
  transform: "scaleX(-1)",
};

const rotatedArrowStyles = {
  left: {
    transform: "rotate(-90deg)",
  },
  right: {
    transform: "rotate(90deg)",
  },
  bottom: {
    transform: "scaleY(-1)",
  },
};

const Editor = ({
  isEdit,
  setIsEdit,
  src,
  setAvatarFile,
  setIsEditorImgHidden,
}: Props) => {
  const intl = useGetLocale();

  const [cropper, setCropper] = useState<Cropper>();
  const [cropData, setCropData] = useState<string>(noAvatarUrl);

  const getCropData = () => {
    if (cropper) {
      setCropData(
        cropper.getCroppedCanvas({ maxHeight: 400, maxWidth: 400 }).toDataURL(),
      );

      setIsEditorImgHidden(false);
    }
  };

  const saveAvatar = () => {
    if (cropper) {
      cropper
        .getCroppedCanvas({ maxHeight: 400, maxWidth: 400 })
        .toBlob((blob) => {
          if (blob) {
            const blobFile = blobToFile(blob, "edited avatar");
            setAvatarFile(blobFile);
          }
        }, "image/png");

      setIsEdit(false);
      setIsEditorImgHidden(true);
    }
  };

  const moveLeft = useCallback(() => {
    if (cropper) {
      cropper.move(-50, 0);
    }
  }, [cropper]);

  const moveRight = useCallback(() => {
    if (cropper) {
      cropper.move(50, 0);
    }
  }, [cropper]);

  const moveTop = useCallback(() => {
    if (cropper) {
      cropper.move(0, -50);
    }
  }, [cropper]);

  const moveBottom = useCallback(() => {
    if (cropper) {
      cropper.move(0, 50);
    }
  }, [cropper]);

  const rotateLeft = useCallback(() => {
    if (cropper) {
      cropper.rotate(-90);
    }
  }, [cropper]);

  const rotateRight = useCallback(() => {
    if (cropper) {
      cropper.rotate(90);
    }
  }, [cropper]);

  useEffect(() => {
    if (!isEdit) {
      setIsEditorImgHidden(true);
    }
  }, [isEdit]);

  useEffect(() => {
    if (!isEdit) {
      setCropData(noAvatarUrl);
    }
  }, [src]);

  return (
    <div className={css.root}>
      <div className={css.editor}>
        <ReactCropper
          className={css.cropper}
          zoomTo={0.1}
          initialAspectRatio={1}
          aspectRatio={1}
          zoomable={false}
          preview=".img-preview"
          src={src}
          viewMode={1}
          guides={true}
          minCropBoxHeight={10}
          minCropBoxWidth={10}
          background={false}
          responsive={true}
          autoCropArea={1}
          onInitialized={(instance) => {
            try {
              setCropper(instance);
            } catch (error) {
              track()?.errors(error);

              notifyBarManager.error({
                message: "Something went wrong",
                timeOut: 4000,
              });
            }
          }}
        />
      </div>
      <div className={css.panel}>
        <div className={css.buttonsWrapper}>
          <div className={css.buttonBox}>
            <Button onClick={rotateLeft}>
              <RotateLeftIcon />
            </Button>
            <Button onClick={rotateRight}>
              <RotateLeftIcon style={rotatedIconStyle} />
            </Button>
          </div>

          <div className={css.movementBox}>
            <Button
              className={clsx(css.moveButtons, css.buttonUp)}
              onClick={moveTop}>
              <UpArrow />
            </Button>
            <Button
              className={clsx(css.moveButtons, css.buttonBottom)}
              onClick={moveBottom}>
              <UpArrow style={rotatedArrowStyles.bottom} />
            </Button>
            <Button
              className={clsx(css.moveButtons, css.buttonLeft)}
              onClick={moveLeft}>
              <UpArrow style={rotatedArrowStyles.left} />
            </Button>
            <Button
              className={clsx(css.moveButtons, css.buttonRight)}
              onClick={moveRight}>
              <UpArrow style={rotatedArrowStyles.right} />
            </Button>
          </div>

          <Button onClick={getCropData}>
            {intl.formatMessage({
              id: "settings.profile.avatar.buttons.crop",
            })}
          </Button>
          <Button
            onClick={saveAvatar}
            className={clsx({ [css.disableImage]: !cropData })}>
            {intl.formatMessage({
              id: "settings.profile.avatar.buttons.save",
            })}
          </Button>
        </div>
        <div className={clsx(css.imgWrapper)}>
          <p className={css.text}>
            {intl.formatMessage({
              id: "settings.profile.avatar.data.preview",
            })}
          </p>
          <img
            src={cropData}
            alt="Result"
            className={clsx(css.previewImage, css.cropper)}
          />
        </div>
      </div>
    </div>
  );
};

export default Editor;
