import React, { useCallback, useEffect } from "react";
import { useGetLocale } from "providers";

import { useFetch } from "hooks";
import { CircularProgress, Typography } from "components";

import Disabled from "./Disabled";
import Enabled from "./Enabled";
import css from "./style.module.scss";
import { modalsLoaderColor } from "sass/styleVariables";

export interface TFA {
  message: string;
  qr_code_url: string;
  success: boolean;
  tfa_enabled: string;
  tfa_key: string;
}

const Get2FA = () => {
  const intl = useGetLocale();

  const [fetch, { data, loading }] = useFetch<TFA>(
    "account/current-tfa-information",
  );

  useEffect(() => {
    fetch();
  }, [fetch]);

  const refetch = useCallback(() => {
    fetch();
  }, [fetch]);

  return (
    <div className={css.wrapper}>
      <Typography variant="h2" className={css.title}>
        {intl.formatMessage({ id: "settings.tfa.title" })}
      </Typography>

      {data ? (
        <div>
          {data.tfa_enabled === "0" && data.tfa_key ? (
            <Disabled TFAData={data} refetch={refetch} loading={loading} />
          ) : (
            <Enabled refetch={refetch} loading={loading} />
          )}
        </div>
      ) : (
        <div className={css.loadingWrapper}>
          <CircularProgress size={70} color={modalsLoaderColor} />
        </div>
      )}
    </div>
  );
};

export default Get2FA;
