import React, { useEffect, useMemo, useRef, useState } from "react";
import clsx from "clsx";
import { useGetLocale } from "providers";

import { Button } from "components";

import ImgInput from "./components/ImgInput";
import Editor from "./components/Editor";
import css from "./style.module.scss";

const AvatarChange = () => {
  const intl = useGetLocale();

  const [imgUrl, setImgUrl] = useState("");
  const [isEdit, setIsEdit] = useState(false);
  const [contentHeight, setContentHeight] = useState<number | undefined>();
  const [imgSizes, setImgSizes] = useState({
    w: 0,
    h: 0,
  });

  const [isEditorImgHidden, setIsEditorImgHidden] = useState(true);
  const [avatarFile, setAvatarFile] = useState<File>();

  const inputRef = useRef<HTMLInputElement>(null);
  const imgRef = useRef<HTMLDivElement>(null);
  const editorRef = useRef<HTMLDivElement>(null);

  const changeImgSize = (w: number, h: number) => setImgSizes({ w, h });

  useEffect(() => {
    setTimeout(() => {
      const editorHeight = editorRef.current?.offsetHeight || 0;
      const imgHeight = imgRef.current?.offsetHeight || 0;

      const newHeight = Math.max(editorHeight, imgHeight);

      setContentHeight(newHeight + 5);
    }, 0);
  }, [
    isEdit,
    imgRef,
    editorRef,
    avatarFile,
    isEditorImgHidden,
    imgUrl,
    imgSizes,
  ]);

  const contentStyle = useMemo(
    () => (contentHeight ? { height: contentHeight } : {}),
    [contentHeight],
  );

  return (
    <div className={css.root}>
      <div className={css.contentBox} style={contentStyle}>
        <div
          ref={imgRef}
          className={clsx(css.content, {
            [css.imgHide]: isEdit,
          })}>
          <ImgInput
            inputRef={inputRef}
            setImgUrl={setImgUrl}
            imgUrl={imgUrl}
            avatarFile={avatarFile}
            setAvatarFile={setAvatarFile}
            changeImgSize={changeImgSize}
            imgSizes={imgSizes}
          />
        </div>

        <div
          ref={editorRef}
          className={clsx(css.content, css.editor, {
            [css.imgHide]: !isEdit,
          })}>
          <Editor
            isEdit={isEdit}
            setIsEdit={setIsEdit}
            src={imgUrl}
            setAvatarFile={setAvatarFile}
            setIsEditorImgHidden={setIsEditorImgHidden}
          />
        </div>
      </div>

      {imgUrl && (
        <Button
          className={css.button}
          onClick={() => {
            setIsEdit(!isEdit);
          }}>
          {isEdit
            ? intl.formatMessage({
                id: "settings.profile.avatar.buttons.change",
              })
            : intl.formatMessage({
                id: "settings.profile.avatar.buttons.edit",
              })}
        </Button>
      )}
    </div>
  );
};

export default AvatarChange;
