import React, { useEffect, useState } from "react";
import { useGetLocale } from "providers";

import { useFetch } from "hooks";
import {
  Divider,
  FormHelperText,
  notifyBarManager,
  Typography,
} from "components";

import { Input, InputProps } from "pages/ModalPages/componentsProgressive";
import { SubmitButton } from "pages/Auth/components";
import css from "./style.module.scss";

interface Password {
  message: string;
}

const PasswordChange = () => {
  const intl = useGetLocale();

  const [fetch, { loading, data, error }] = useFetch<Password>(
    "account/change-password",
  );

  const [oldPassword, setOldPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [passwordRepeat, setPasswordRepeat] = useState("");
  const [passwordError, setPasswordError] = useState<string>();

  const handleChangeOldPassword: InputProps["onChange"] = (event) => {
    setOldPassword(event.target.value);
    setPasswordError("");
  };

  const handleChangeNewPassword: InputProps["onChange"] = (event) => {
    setNewPassword(event.target.value);
    setPasswordError("");
  };

  const handleChangePasswordRepeat: InputProps["onChange"] = (event) => {
    setPasswordRepeat(event.target.value);
    setPasswordError("");
  };

  useEffect(() => {
    if (loading) {
      return;
    }

    setPasswordError(error);
  }, [error, loading]);

  const handleSubmit: React.FormEventHandler<HTMLFormElement> = async (
    event,
  ) => {
    event.preventDefault();

    if (newPassword && newPassword !== passwordRepeat) {
      setPasswordError(
        intl.formatMessage({ id: "settings.password.errors.notMatch" }),
      );
      return;
    }

    fetch({
      variables: {
        old_password: oldPassword,
        new_password: newPassword,
      },
    });
  };

  useEffect(() => {
    if (loading || error || !data?.message) {
      return;
    }

    setOldPassword("");
    setNewPassword("");
    setPasswordRepeat("");
    setPasswordError("");

    notifyBarManager.success({
      message: data.message,
      timeOut: 4000,
    });
  }, [data, error, loading]);

  return (
    <div className={css.wrapper}>
      <form className={css.innerWrapper} onSubmit={handleSubmit}>
        <Typography variant="h2" className={css.title}>
          {intl.formatMessage({ id: "settings.password.title" })}
        </Typography>
        <Input
          id="settings-change-password-input"
          label={intl.formatMessage({
            id: "settings.password.inputOldPassword",
          })}
          value={oldPassword}
          disabled={loading}
          onChange={handleChangeOldPassword}
          type="password"
          required
        />

        <Divider className={css.divider} />

        <Input
          id="settings-change-password-input"
          label={intl.formatMessage({
            id: "settings.password.inputNewPassword",
          })}
          value={newPassword}
          disabled={loading}
          onChange={handleChangeNewPassword}
          type="password"
          required
        />
        <Input
          id="settings-change-password-input"
          label={intl.formatMessage({
            id: "settings.password.inputPasswordRepeat",
          })}
          value={passwordRepeat}
          disabled={loading}
          onChange={handleChangePasswordRepeat}
          type="password"
          required
        />

        <div className={css.errorWrapper}>
          <FormHelperText error>{passwordError}</FormHelperText>
        </div>

        <SubmitButton
          type="submit"
          loading={loading}
          className={css.submitButton}>
          {intl.formatMessage({ id: "settings.password.button" })}
        </SubmitButton>
      </form>
    </div>
  );
};

export default PasswordChange;
