import React, { FC, useRef, CSSProperties } from "react";

import { CircularProgress } from "components";

import { ReactComponent as SuccessIcon } from "assets/icons/check-circle-outline.svg";

import css from "./style.module.scss";

interface Props {
  status: "loading" | "none" | "success";
}

const LoadingStatus: FC<Props> = ({ status }) => {
  const rootRef = useRef<HTMLDivElement>(null);

  const progressStyle: CSSProperties = (() => {
    if (rootRef.current) {
      const sizes =
        Math.min(rootRef.current.offsetHeight, rootRef.current.offsetWidth) / 2;

      return {
        height: sizes,
        width: sizes,
      };
    }

    return {};
  })();

  return (
    <div className={css.root} ref={rootRef}>
      {status === "loading" && (
        <>
          <div className={css.statusWrapper} />
          <CircularProgress style={progressStyle} />
        </>
      )}

      {status === "success" && (
        <>
          <div className={css.statusWrapper} />
          <SuccessIcon className={css.okIcon} />
        </>
      )}
    </div>
  );
};

export default LoadingStatus;
