import React, {
  ChangeEvent,
  Dispatch,
  RefObject,
  SetStateAction,
  useEffect,
  useRef,
  useState,
} from "react";
import clsx from "clsx";

import { ReactComponent as DownloadIcon } from "assets/icons/rectangle.svg";
import { notifyBarManager } from "components";

import LoadingStatus from "./components/LoadingStatus";
import ErrorMessage from "./components/ErrorMessage";
import SendImg from "./components/SendImg";
import css from "./style.module.scss";
import { track } from "utils";

export type ImgStatus = "error" | "loading" | "good" | "wrong";
interface Props {
  inputRef: RefObject<HTMLInputElement>;
  setImgUrl: Dispatch<SetStateAction<string>>;
  imgUrl: string;

  avatarFile: File | undefined;
  setAvatarFile: Dispatch<SetStateAction<File | undefined>>;
  changeImgSize: (w: number, h: number) => void;
  imgSizes: {
    w: number;
    h: number;
  };
}

const ImgInput = ({
  inputRef,
  setImgUrl,
  imgUrl,
  avatarFile,
  setAvatarFile,
  changeImgSize,
  imgSizes,
}: Props) => {
  const imgRef = useRef<HTMLImageElement>(null);

  const [imageErrorStatus, setImageErrorStatus] =
    useState<ImgStatus>("loading");
  const [imgSendStatus, setImgSendStatus] = useState<
    "none" | "loading" | "success"
  >("none");

  const [isImgHide, setIsImgHide] = useState(true);
  const [imageData, setImageData] = useState({
    weight: 0,
    height: 0,
    width: 0,
  });

  useEffect(() => {
    if (imageData.weight) {
      if (
        !(
          imageData.height <= 400 &&
          imageData.width <= 400 &&
          imageData.weight <= 1024000
        )
      ) {
        setImageErrorStatus("error");
      } else {
        setImageErrorStatus("good");
      }
    } else {
      setImageErrorStatus("error");
    }
  }, [imageData]);

  useEffect(() => {
    try {
      if (avatarFile) {
        const reader = new FileReader();
        reader.readAsDataURL(avatarFile);
        reader.addEventListener("load", () => {
          try {
            if (typeof reader.result === "string") {
              setIsImgHide(false);
              setImgSendStatus("none");
              setImgUrl(reader.result);
            }
          } catch (error) {
            track()?.errors(error);

            setImageErrorStatus("wrong");
          }
        });
        setImageErrorStatus("loading");
      } else if (!isImgHide) {
        setIsImgHide(true);
      }
    } catch (error) {
      track()?.errors(error);

      setImageErrorStatus("wrong");
    }
  }, [avatarFile, imgRef]);

  const getImage = (event: ChangeEvent<HTMLInputElement>) => {
    try {
      if (event.target.files) {
        const file = event.target.files[0];

        const checkType =
          file.type === "image/png" ||
          file.type === "image/jpeg" ||
          file.type === "image/gif";
        if (!checkType) {
          setAvatarFile(undefined);

          notifyBarManager.error({
            message: "Wrong image format",
            timeOut: 4000,
          });

          return;
        }

        setImgSendStatus("none");
        if (file) {
          setAvatarFile(file);
          if (inputRef.current) {
            inputRef.current.type = "text";
            inputRef.current.type = "file";
          }
        }
      }
    } catch (error) {
      track()?.errors(error);
      setImageErrorStatus("wrong");
    }
  };

  useEffect(() => {
    if (!imgUrl) {
      setIsImgHide(true);
    } else {
      if (
        imgRef.current &&
        !!imgRef.current.src &&
        avatarFile &&
        imgSizes.h &&
        imgSizes.w
      ) {
        setImageData({
          weight: avatarFile.size,
          width: imgSizes.w,
          height: imgSizes.h,
        });
      }
    }
  }, [imgUrl, imgRef.current, avatarFile, imgSizes]);

  const imgOnload = () => {
    if (imgRef.current)
      changeImgSize(imgRef.current.naturalWidth, imgRef.current.naturalHeight);
  };
  return (
    <div className={css.root}>
      <label className={css.label}>
        <DownloadIcon />

        <input
          type="file"
          accept="image/*"
          ref={inputRef}
          className={css.loaderInput}
          onChange={getImage}
        />
      </label>

      <div className={css.loadedImageWrapper}>
        <img
          alt="preview"
          ref={imgRef}
          src={imgUrl}
          className={clsx(css.loadedImage, {
            [css.hideImage]: isImgHide,
          })}
          onLoad={imgOnload}
        />
        <LoadingStatus status={imgSendStatus} />
      </div>

      <ErrorMessage image={avatarFile} imageError={imageErrorStatus} />
      {avatarFile && (
        <SendImg
          imageErrorStatus={imageErrorStatus}
          avatarFile={avatarFile}
          setImgSendStatus={setImgSendStatus}
          imgSendStatus={imgSendStatus}
        />
      )}
    </div>
  );
};

export default ImgInput;
