import React, { FunctionComponent, useState } from "react";
import SwipeableViews from "react-swipeable-views";
import { useGetLocale } from "providers";

import { ProgressiveModal } from "components";
import { ModalsProps } from "config/routes";

import Template from "pages/ModalPages/componentsProgressive/Template";
import { DialogHeader } from "../../componentsProgressive";

import PasswordChange from "./components/Password";
import AvatarChange from "./components/Avatar";
import Switcher from "./components/Switcher";
import MailChange from "./components/EMail";
import Get2FA from "./components/2FA";

const Settings: FunctionComponent<ModalsProps> = ({ isOpen }) => {
  const [selectedIndex, setSelectedIndex] = useState(0);
  const intl = useGetLocale();

  return (
    <ProgressiveModal
      open={isOpen}
      width={530}
      aria-labelledby="settings-dialog-title"
      aria-describedby="settings-dialog-description">
      <DialogHeader title={intl.formatMessage({ id: "settings.title" })} />

      <Template>
        <Switcher index={selectedIndex} setIndex={setSelectedIndex} />

        <SwipeableViews disabled disableLazyLoading index={selectedIndex}>
          <MailChange />
          <AvatarChange />
          <PasswordChange />
          <Get2FA />
        </SwipeableViews>
      </Template>
    </ProgressiveModal>
  );
};

export default Settings;
