import React, { useEffect, useState } from "react";
import { SwitchBox } from "components";
import css from "./style.module.scss";
import { useFetch } from "hooks";

interface Response {
  is_hidden: string;
  success: boolean;
}

const HideProfileSwitcher = () => {
  const [fetchCurrentStatus, { data, error, loading, called }] =
    useFetch<Response>("account/current-hidden-status");
  const [fetchChangeStatus] = useFetch<Response>(
    "account/change-hidden-status",
  );

  const [isHidden, setIsHidden] = useState<boolean>(false);

  useEffect(() => {
    fetchCurrentStatus();
  }, []);

  useEffect(() => {
    if (!data || error) {
      return;
    }

    setIsHidden(data.is_hidden === "1");
  }, [data, called, error, loading]);

  const onChangeSwitch = async () => {
    const response = await fetchChangeStatus({
      variables: {
        is_hidden: isHidden ? "0" : "1",
      },
    });

    if (!response.data || response.error) {
      return;
    }

    setIsHidden(response.data.is_hidden === "1");
  };

  return (
    <div className={css.root}>
      <div className={css.text}>Stay private?</div>
      <SwitchBox
        isChecked={isHidden}
        switchHandleChange={onChangeSwitch}
        text={{
          off: "OFF",
          on: "ON",
        }}
        classes={{
          root: css.switchBoxRoot,
          switch: {
            track: css.switchThumb,
            toggle: css.switchToggle,
            root: css.switchRoot,
          },
          text: css.switchText,
        }}
      />
    </div>
  );
};

export default HideProfileSwitcher;
