import React from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import { ReactComponent as LockIcon } from "assets/icons/lock-sign.svg";
import { ReactComponent as AtIcon } from "assets/icons/at-sign.svg";
import { ReactComponent as ShieldIcon } from "assets/icons/shield-sign.svg";
import { ReactComponent as UserIcon } from "assets/icons/user-sign.svg";

import { Button, Typography } from "components";

import css from "./style.module.scss";

interface SwitchSectionProps {
  index: number;
  setIndex: (index: number) => void;
}

const SwitchSection = ({ index, setIndex }: SwitchSectionProps) => {
  const intl = useGetLocale();

  const switchButtons = [
    {
      name: intl.formatMessage({ id: "settings.email.switcher" }),
      Icon: LockIcon,
    },
    {
      name: intl.formatMessage({ id: "settings.profile.switcher" }),
      Icon: ShieldIcon,
    },
    {
      name: intl.formatMessage({ id: "settings.password.switcher" }),
      Icon: AtIcon,
    },
    {
      name: intl.formatMessage({ id: "settings.tfa.switcher" }),
      Icon: UserIcon,
    },
  ];

  return (
    <div className={css.wrapper}>
      {switchButtons.map(({ name, Icon }, i) => (
        <Button
          key={`settings-switcher-button-${i}`}
          onClick={() => setIndex(i)}
          className={clsx(css.button, [index === i && css.selectedButton])}>
          <div className={css.iconWrapper}>
            <Icon className={css.icon} />
          </div>
          <Typography className={css.label}>{name}</Typography>
        </Button>
      ))}
    </div>
  );
};

export default SwitchSection;
